<template>
  <div style="display: flex;">
      <span class="input-label" style="min-width: 70px;">{{name}}：</span>
      <div>  <!--在控件外层加一个壳子，适配ie-->
        <DatePicker type="date"  :options="options1" v-model='startTime' @on-change="timeChange('start',$event)"  placement="bottom-start" placeholder="开始时间" class="input-width"></DatePicker>
      </div>
      <span class="time-line"> — </span>
      <div>
        <DatePicker type="date" :options="options2" v-model='endTime' @on-change="timeChange('end',$event)"  placement="bottom-start" placeholder="结束时间" class="input-width"></DatePicker>
      </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        options1:{},//时间范围配置
        options2:{},//时间范围配置
        startTime:'',//开始时间
        endTime:'',//结束时间
      }
    },
    mounted() {
      this.$nextTick(function () {
      })
    },
    watch:{
    },
    //名字
    props:{
      name:[Number,String],
    },
    methods: {
      //重置数据
      resetTime(){
        this.options1={}//时间范围配置
        this.options2={}//时间范围配置
        this.startTime=''//开始时间
        this.endTime=''//结束时间
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="start"){
          this.startTime=time
          this.options2={
            disabledDate (date) {
              return date && date.valueOf() < new Date(time) - 86400000;
            }
          }
          this.$emit("start-time",this.startTime)
        }else if(e=="end"){
          this.endTime=time
          this.options1={
            disabledDate (date) {
              return date && date.valueOf() > new Date(time) ;
            }
          }
          this.$emit("end-time",this.endTime)
        }
      },
    },
  }
</script>
<style lang="less" scoped>
</style>



